.oContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  height: 100vh;
}

.oContainer .col {
  -ms-flex: 1 1;
      flex: 1 1;
}

.top {
  min-height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.oContainer .left {
  /* border: 1px solid green; */
  -ms-flex: 1 1;
      flex: 1 1;
  max-width: 30vw;

}

.oContainer .right {
  /* border: 1px solid purple; */
  -ms-flex: 3 1;
      flex: 3 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.dropdown {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px 15px;
  -webkit-box-shadow: 3px 3px 15px rgba(0,0,0, .2);
          box-shadow: 3px 3px 15px rgba(0,0,0, .2);
  font-size: .8em;
  font-weight: 700;
  /* border: 1px solid #bec3c7; */
  position: absolute;
}

.drop-button {
  background: none;
  border: none;
  cursor: pointer;
  font-weight: 700;
  font-size: .8em;
  color: rgba(41, 127, 184, 1);
  z-index: 300;
}

.dropdown a {
  text-decoration: none;
}



/* ICONS */

.icon {
  background-color: #fff;
  border: none;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.icon svg {
  height: 20px;
  width: 20px;
  -webkit-transition: .1s linear;
  -o-transition: .1s linear;
  transition: .1s linear;
}

.delete_icon-1, .delete_icon-2, .delete_icon-3 {
  fill: none;
  stroke: rgba(21, 107, 164, 1);
  stroke-miterlimit: 10;
}

.delete_icon-2, .delete_icon-3 {
  stroke-width: 10px;
}

.delete_icon-3 {
  stroke-linecap: round;
}

.icon:hover svg .delete_icon-2, .icon:hover svg .delete_icon-3 {
  stroke: #496583;
  fill: rgba(232,76,61, 1);
  /* fill: #e84c3d; */
}



