.search {
  /* border: 3px dashed tomato; */
  display: -ms-flexbox;
  display: flex;
  padding: 0px 20px;
  width: auto;
  border-bottom:  1px solid #ecf0f1;
  border-right:  1px solid #ecf0f1;
}

.search input {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 20px;
  width: 100%;
  border: 1px solid #bec3c7;
  border-radius: 20px;
  padding: 5px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media only screen and (max-width: 700px) {
  .search {
    padding: 0px 5px;
  }
  .search input {
    margin: 0px;
    width: auto;
  }
}



