.noteMenu {
  /* border: 3px dashed lightgreen; */
  border-bottom:  1px solid #ecf0f1;
  /* border-left:  1px solid #ecf0f1; */
  display: -ms-flexbox;
  display: flex;
}

.auth {
  padding: 0px 15px;
}

.github-login {
  background: -webkit-gradient(linear, left top, left bottom, from(#444), to(#24292E));
  background: -webkit-linear-gradient(#444, #24292E);
  background: -o-linear-gradient(#444, #24292E);
  background: linear-gradient(#444, #24292E);
  color: #fff;
  text-decoration: none;
  font-size: .7em;
  border-radius: 10px;
  padding: 5px 8px;
}

.github-login:active {
  background: -webkit-gradient(linear, left top, left bottom, from(#24292E), to(#444));
  background: -webkit-linear-gradient(#24292E, #444);
  background: -o-linear-gradient(#24292E, #444);
  background: linear-gradient(#24292E, #444);
}

.noteMenu-options {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  padding: 0px 30px;
  -ms-flex-align: start;
      align-items: flex-start;
}



